<template>
  <div class="header-nav">
    <div class="content">
      {{title}}
    </div>
    <div class="right">
      {{time}}
    </div>
  </div>
</template>
<script>
export default {
  props: ['title'],
  data() {
    return {
      time: ''
    }
  },
  mounted() {
    setInterval(() => {
      this.time = this.$moment(new Date()).format('YYYY-MM-DD hh:mm:ss')
    }, 1000)
  },
  methods: {}
}
</script>

