<template>
  <div id="app" :style="{ height: WindowHeight + 'px' }">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      WindowHeight: 0
    }
  },
  mounted() {
    this.$nextTick(() => {
      // 修改文档宽高比导致的底部白屏问题
      const documentHeight = document.body.scrollHeight
      const windowHeight = window.innerHeight
      this.WindowHeight = documentHeight > windowHeight ? documentHeight : windowHeight
    })
  }
}
</script>

<style lang="scss">
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 100%;
    height: 100%;
  }
</style>
