<script>
import echarts from 'echarts'
import sHeader from '../commom/header'
import { DataSvr } from '../../service/DataSvr'

export default {
  components: {
    sHeader
  },
  data() {
    return {
      title: '',
      options: [], // 左侧列表
      SelectionPeopleIndex: 'null',
      DataSvr: new DataSvr(),
      cumulativeNumberData: [], // 累计人数
      abnormalData: [], // 体温异常折线图
      heatTrendStatisticsData: [], // 体温异常折线图
      getHeatUserPageData: [], //   累计检测高热人数
      deviceId: '',
      highTemperatureData: [], // 高温人次
      timerLeft: null, // 左侧栏倒计时
      cumulativeNumberTimer: null, // 累计人数倒计时
      getHeatUserPageTimer: null, // 获取体温异常人员-点位实时状态倒计时
      reloadTimer: null, // 刷新页面计时器
      barChart: null
    }
  },
  mounted() {
    if (process.env.NODE_ENV === 'development') {
      // 开发模式 东方天呈ID
      this.deviceId = '3d406cc7-53d4-432c-be79-ace661712935 '
    } else {
      this.deviceId = this.$route.query.deviceId
    }
    this.barChart = echarts.init(document.getElementById('main'))
    // this.name = this.$route.query.name;
    // this.title = `${this.name}点位体温实时状态`;
    if (this.deviceId) {
      this.heatTrendStatisticsBy()
      this.getDeviceAddressById()
      this.leftData()
      this.cumulativeNumber()
      this.getHeatUserPageBy()
    }
    if (this.reloadTimer) {
      clearInterval(this.reloadTimer)
    } else {
      this.reloadTimer = setInterval(() => {
        location.reload()
      }, 1000 * 60 * 60)
    }
  },
  methods: {
    // 获取title
    getDeviceAddressById() {
      this.DataSvr.getDeviceAddressById(this.deviceId).then(res => {
        if (res) {
          this.title = res + '点位体温实时状态'
        }
      }).catch()
    },
    // 折线图
    showProvince(Vo) {
      // const myChart = this.$echarts.init(document.getElementById('main'))
      this.barChart.setOption({
        'title': {
          'text': '体温异常人次',
          x: '4%',
          textStyle: {
            color: '#fff',
            fontSize: '16'
          }
        },
        'tooltip': {
          'trigger': 'axis',
          'axisPointer': {
            'type': 'shadow',
            textStyle: {
              color: '#fff'
            }

          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: Vo.DataX,
          axisLabel: {
            margin: 10,
            textStyle: {
              fontSize: 12,
              color: '#fff'
            }
          }

        },
        yAxis: {
          min: 0, // 设置y轴刻度的最小值
          max: 50, // 设置y轴刻度的最大值
          splitNumber: 5, // 设置y轴刻度间隔个数
          axisLabel: {
            margin: 10,
            textStyle: {
              fontSize: 12,
              color: '#fff'
            }
          },
          splitLine: {
            lineStyle: {
              type: 'solid',
              color: '#1FF1FFFF'
            }
          },
          type: 'value',
          axisLine: {
            lineStyle: {
              color: '#305D7BFF'
            }
          }
        },
        series: [{
          data: Vo.DataY,
          lineStyle: {
            normal: {
              color: '#1FF1FFFF'
            }
          },
          type: 'line',
          areaStyle: {
            color: '#1FF1FF'
          }
        }]
      })
    },
    // 左边栏数据
    leftData() {
      clearInterval(this.timerLeft)
      this.DataSvr.getRecRecordPage(this.deviceId, 1, 20).then(res => {
        this.options = res.records
        for (let i = 0; i < res.records.length; i++) {
          // this.highTemperatureData.push(res.records[0]);
          if (res.records[i].temperature > 37.3) {
            // this.highTemperatureData.push(res.records[i]);
            res.records[i].states = true
          } else {
            res.records[i].states = false
          }
        }
        this.timerLeft = setInterval(() => {
          this.leftData()
        }, 3000)
        // if (this.highTemperatureData.length > 0) {
        //     localStorage.setItem('abnormal', JSON.stringify(this.highTemperatureData[0]));
        // }
      }).catch(() => {
        clearInterval(this.timerLeft)
      })
    },
    // 累计人数
    cumulativeNumber() {
      clearInterval(this.cumulativeNumberTimer)
      this.DataSvr.statistics(this.deviceId).then(res => {
        this.cumulativeNumberData = res
        this.cumulativeNumberTimer = setInterval(() => {
          this.cumulativeNumber()
        }, 3000)
      }).catch(() => {
        clearInterval(this.cumulativeNumberTimer)
      })
    },
    // 高亮选中
    SelectionPeople(val) {
      this.SelectionPeopleIndex = val
    },
    // 获取体温检测统计数据-高热人群走势
    heatTrendStatisticsBy() {
      this.DataSvr.heatTrendStatistics(this.deviceId).then(res => {
        if (res) {
          const y = []; const x = []; let total = 0
          for (let i = 0; i < res.length; i++) {
            y.push(res[i].temperatureHeatTotal)
            x.push(this.$moment(res[i].date).format('MM-DD'))
            total += res[i].temperatureHeatTotal
          }
          this.heatTrendStatisticsData.DataY = y
          this.heatTrendStatisticsData.DataX = x
          this.heatTrendStatisticsData.total = total
          this.showProvince(this.heatTrendStatisticsData)
        }
      }).catch()
    },
    // 获取体温异常人员-点位实时状态
    getHeatUserPageBy() {
      clearInterval(this.getHeatUserPageTimer)
      this.DataSvr.getHeatUserPage(this.deviceId, 1, 20).then(res => {
        this.getHeatUserPageData = res.records
        this.getHeatUserPageTimer = setInterval(() => {
          this.getHeatUserPageBy()
        }, 3000)
      }).catch(() => {
        clearInterval(this.getHeatUserPageTimer)
      })
    },
    toLink(item) {
      if (item.states) {
        this.$router.push({
          path: '/personnelBehavior',
          query: {
            recRecordId: item.id,
            recDatetime: item.recDatetime,
            name: `${this.name}点位体温实时状态`
          }
        })
      }
    },
    Hyperthermia(item) {
      this.$router.push({
        path: '/personnelBehavior',
        query: {
          recRecordId: item.id,
          recDatetime: item.recDatetime,
          name: `${this.name}点位体温实时状态`
        }
      })
    }
  },
  beforeDestroy() {
    clearInterval(this.timerLeft)
    clearInterval(this.cumulativeNumberTimer)
    clearInterval(this.getHeatUserPageTimer)
    clearInterval(this.reloadTimer)
  },
  destroyed() {
    clearInterval(this.timerLeft)
    clearInterval(this.cumulativeNumberTimer)
    clearInterval(this.getHeatUserPageTimer)
    clearInterval(this.reloadTimer)
  }
}
</script>

