import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index/index'
import realTime from '../pages/realTime/realTime'
import personnelBehavior from '../pages/personnelBehavior/personnelBehavior'
import dynamic from '../pages/dynamic/dynamic'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/realTime',
      name: 'realTime',
      component: realTime
    },
    {
      path: '/dynamic',
      name: 'dynamic',
      component: dynamic
    },
    {
      path: '/personnelBehavior',
      name: 'personnelBehavior',
      component: personnelBehavior
    },
    {
      path: '/404',
      component: 404,
      name: '404'
    },
    {
      path: '/*',
      redirect: '/index'
    }
  ]
})
