import axios from 'axios'
// import { Message, MessageBox } from 'element-ui'
// import { Loading } from 'element-ui'
import cryptoJs from 'crypto-js'

const baseURL = process.env.NODE_ENV === 'development' ? 'http://liaodongoray.xicp.net:46873' : ' http://liaodongoray.xicp.net:46873'
// let baseURL = process.env.NODE_ENV === 'development' ? 'http://q2899m0062.goho.co' : 'http://q2899m0062.goho.co';

const service = axios.create({
  baseURL: baseURL, // api 的 base_url
  timeout: 10000, // request timeout
  responseType: 'text',
  headers: {}

})

const pending = [] // 声明一个数组用于存储每个ajax请求的取消函数和ajax标识
const CancelToken = axios.CancelToken
// const removePending = (config) => {
//   for (const p in pending) {
//     if (pending[p].u === config.url + '&' + config.method) { // 当当前请求在数组中存在时执行函数体
//       pending[p].f() // 执行取消操作
//       pending.splice(p, 1) // 把这条记录从数组中移除
//     }
//   }
// }

// 添加请求拦截器
service.interceptors.request.use(config => {
  // removePending(config); //在一个ajax发送前执行一下取消操作
  config.cancelToken = new CancelToken((c) => {
    // 这里的ajax标识我是用请求地址&请求方式拼接的字符串，当然你可以选择其他的一些方式
    pending.push({ u: config.url + '&' + config.method, f: c })
  })
  // if (config.params) {
  //     let arr = Object.keys(config.params);
  //     for (let i = 0; i < arr.length; i++) {
  //         if (config.params[arr[i]]) {
  //             config.params[arr[i]] = encodeURIComponent(config.params[arr[i]]);
  //         }
  //
  //     }
  // }
  const timestamp = new Date().getTime()
  const clientId = 'tian-cheng-admin'
  const key = 'da74588912504563e464ffe8956de786'
  config['headers'] = {
    'X-clientId': clientId,
    'X-signature': cryptoJs.MD5(clientId + timestamp + key).toString(),
    'X-timestamp': timestamp
  }

  return config
},
error => {
  return Promise.reject(error)
})

// http响应拦截
service.interceptors.response.use((res) => {
  // Loading.service({fullscreen: true});
  if (res.data.respCode === '00') {
    // Loading.service({fullscreen: true}).close();
    return Promise.resolve(res.data.data)
  } else {
    // Loading.service({fullscreen: true}).close();
    // Message.error({message: res.data.message, showClose: true,});
    return Promise.reject(res.data)
  }
}, (err) => {
  // Loading.service({fullscreen: true});
  // Loading.service({fullscreen: true}).close();
  // Message.error({message: err.response.data.message, showClose: true,});
  // Loading.service({ fullscreen: true }).close();

  return Promise.reject(err)
})

export default { service, baseURL }
