<script>
import axios from 'axios'
import echarts from 'echarts'
/*   import uploadedData from "../../assets/json/sichuan"; */
import sHeader from '../commom/header'
import { DataSvr } from '../../service/DataSvr'

export default {
  components: {
    sHeader
  },
  data() {
    return {
      DataSvr: new DataSvr(),
      title: '四川省发热检测分布图',
      time: ' 2020-02-12 14:29:10',
      statisticsData: {}, // 累积检测人次数据
      mapList: [], // 地图数据
      heatTrendStatisticsList: {}, // 高热人群走势数据
      heatDistributeStatisticsList: [], // 高热人群分布势数据
      areaId: 510000,
      originData: null,
      name: '四川省',
      uploadedData: null,
      mapChart: null,
      lineChart: null
    }
  },
  watch: {
    statisticsData() {
      this.timer()
    }
  },

  mounted() {
    if (this.$route.query.areaId) {
      this.areaId = this.$route.query.areaId
      this.getPos()
    }
    this.getStatisticsData(this.areaId)
    this.getMapStatisticsData(this.areaId)
    this.getHeatTrendStatisticsData(this.areaId)
    this.getHeatDistributeStatisticsData(this.areaId)
    this.mapChart = echarts.init(document.getElementById('main'))
    this.lineChart = echarts.init(document.getElementById('line'))
  },
  methods: {
    // 定时器
    timer() {
      clearTimeout(this.timer)
      return setTimeout(() => {
        this.getStatisticsData(this.areaId)
        this.getHeatTrendStatisticsData(this.areaId)
        this.getHeatDistributeStatisticsData(this.areaId)
      }, 3000)
    },
    // 获取本地json
    getJson(id, mapArr) {
      axios
        .get('/json/' + id + '.json')
        .then(res => {
          this.uploadedData = res.data
          this.showProvince(mapArr, this.uploadedData)
        })
        .catch(() => {
        })
    },
    // 获取初始经纬度等
    getPos() {
      this.DataSvr.getPosition(this.$route.query.areaId)
        .then(res => {
          this.originData = res
          this.name = this.originData.areaName
          this.title = `${this.name}发热检测分布图`
        })
        .catch(() => {
        })
    },
    // 获取累积检测人次数据
    getStatisticsData(id) {
      this.DataSvr.getStatistics(id)
        .then(res => {
          this.statisticsData = res
        })
        .catch(() => {
        })
    },
    // 获取地图数据
    getMapStatisticsData(id) {
      this.DataSvr.getMapStatistics(id)
        .then(res => {
          this.mapList = res
          const mapArr = []
          this.mapList.forEach(item => {
            const mapItem = {
              name: item.areaName,
              value: item.temperatureHeatTotal
            }
            mapArr.push(mapItem)
          })
          this.getJson(id, mapArr)
        })
        .catch(() => {
        })
    },
    // 获取高热人群走势数据
    getHeatTrendStatisticsData(id) {
      this.DataSvr.getHeatTrendStatistics(id)
        .then(res => {
          this.heatTrendStatisticsList = res
          if (this.heatTrendStatisticsList.dateStatistics) {
            const timeArr = []
            const numArr = []
            this.heatTrendStatisticsList.dateStatistics.forEach(item => {
              timeArr.push(this.$moment(item.date).format('MM-DD'))
              numArr.push(item.temperatureHeatTotal)
            })

            this.showLineChart(timeArr, numArr)
          }
        })
        .catch(() => {
        })
    },
    // 获取高热人群分布势数据
    getHeatDistributeStatisticsData(id) {
      this.DataSvr.getIndexHeatDistributeStatistics(id)
        .then(res => {
          this.heatDistributeStatisticsList = res
        })
        .catch(() => {
        })
    },
    // 地图图表
    showProvince(data, uploadedData) {
      // const myChart = this.$echarts.init(document.getElementById('main'))
      const name = 'sj'
      echarts.registerMap(name, uploadedData)
      this.mapChart.setOption({
        color: ['white'],
        visualMap: {
          min: 0,
          max: 10000,
          left: 'right',
          top: 'top',
          // 图例颜色
          pieces: [
            {
              min: 100,
              max: 999,
              label: '100-999人',
              color: '#96301F'
            },
            {
              min: 10,
              max: 99,
              label: '10-99人',
              color: '#CC442F'
            },
            {
              min: 1,
              max: 9,
              label: '1-9人',
              color: '#FD9285'
            },
            {
              min: 0,
              max: 0,
              label: '0人',
              color: '#ffffff'
            }
          ],
          orient: 'vertical',
          itemWidth: 80,
          itemHeight: 16,
          showLabel: true,
          seriesIndex: [0],
          textStyle: {
            color: '#fff'
          }
        },
        toolbox: {
          show: false,
          orient: 'vertical',
          left: 'right',
          top: 'center',
          feature: {
            dataView: {
              readOnly: false
            },
            restore: {},
            saveAsImage: {}
          }
        },
        series: [
          {
            name: '地市',
            type: 'map',
            mapType: name,
            label: {
              normal: {
                show: true
              },
              emphasis: {
                textStyle: {
                  color: '#fff'
                }
              }
            },

            data: data
          }
        ]
      })
    },
    // 折线图
    showLineChart(timeArr, numArr) {
      this.lineChart.setOption({
        grid: {
          top: '16%',
          left: '4%',
          right: '5%',
          bottom: '4%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: timeArr,
          axisLine: {
            lineStyle: {
              // 设置x轴颜色
              color: '#fff'
            }
          }
        },
        yAxis: {
          type: 'value',
          min: 0, // 设置y轴刻度的最小值
          max: Math.max.apply(null, numArr), // 设置y轴刻度的最大值
          axisLine: {
            lineStyle: {
              // 设置x轴颜色
              color: '#fff'
            }
          }
        },
        series: [
          {
            data: numArr,
            type: 'line',
            areaStyle: {}
          }
        ],
        color: ['#1FF1FF']
      })
    }
  },
  destroyed() {
    clearTimeout(this.timer)
    clearTimeout(this.heatTimer)
    echarts.dispose(this.mapChart)
    echarts.dispose(this.lineChart)
  }
}
</script>

