import request from '@/utils/request'

export class DataSvr {
  constructor() {
    this.baseurl = '/adminService/'
  }

  // 页面index  获取初始经纬度等
  getPosition(areaId) {
    return request.post(this.baseurl + 'provinceCheck/getAreaDataById', {
      areaId: areaId
    })
  }

  // 页面index  累积检测人数
  getStatistics(areaId) {
    return request.post(this.baseurl + 'provinceCheck/statistics', {
      areaId: areaId
    })
  }

  // 页面index  体温检测统计数据-地图分布图
  getMapStatistics(areaId) {
    return request.post(this.baseurl + 'provinceCheck/mapStatistics', {
      areaId: areaId
    })
  }

  // 页面index  高热人群走势
  getHeatTrendStatistics(areaId) {
    return request.post(this.baseurl + 'provinceCheck/heatTrendStatistics', {
      areaId: areaId
    })
  }

  // 页面index  四川省高热人群分布
  getIndexHeatDistributeStatistics(areaId) {
    return request.post(this.baseurl + 'provinceCheck/heatDistributeStatistics', {
      areaId: areaId
    })
  }

  // 页面dynamic 体温监测数据列表
  getHeatUser(areaId) {
    return request.post(this.baseurl + 'cityCheck/getRecRecordPage', {
      areaId: areaId,
      page: {
        pageNumber: 1,
        pageSize: 20
      }
    })
  }

  // 页面dynamic  发热点位
  getMapEastes(areaId) {
    return request.post(this.baseurl + 'cityCheck/getEstates', {
      areaId: areaId
    })
  }

  // 页面dynamic  体温高热趋势图
  getHeatDistributeStatistics(areaId) {
    return request.post(this.baseurl + 'cityCheck/heatTrendStatistics', {
      areaId: areaId
    })
  }

  // 页面dynamic  体温高热人数分布
  getCityHeatDistributeStatistics(areaId) {
    return request.post(this.baseurl + 'cityCheck/heatDistributeStatistics', {
      areaId: areaId
    })
  }

  // 页面dynamic  行业疫情
  getEpidemicStatistics(areaId) {
    return request.post(this.baseurl + 'cityCheck/industryEpidemicStatistics', {
      areaId: areaId
    })
  }

  /*     //页面dynamic  最新温度异常人员信息
      getEpidemicStatistics(areaId){
          return request.post(this.baseurl + 'cityCheck/industryEpidemicStatistics', {
              areaId: areaId,
          });
      }     */

  // title
  getDeviceAddressById(deviceId) {
    return request.post(this.baseurl + 'localtionCheck/getDeviceAddressById', {
      deviceId: deviceId
    })
  }

  // realTime

  getRecRecordPage(deviceId, pageNumber, pageSize) { // 获取点位体温实时状态-点位实时状态
    return request.post(this.baseurl + 'localtionCheck/getRecRecordPage', {
      deviceId: deviceId,
      page: {
        pageNumber: pageNumber,
        pageSize: pageSize
      }
    })
  }

  statistics(deviceId) { // 获取体温检测统计数据-点位实时状态
    return request.post(this.baseurl + 'localtionCheck/statistics', {
      deviceId: deviceId
    })
  }

  heatTrendStatistics(deviceId) { // 获取体温检测统计数据-高热人群走势
    return request.post(this.baseurl + 'localtionCheck/heatTrendStatistics', {
      deviceId: deviceId
    })
  }

  getHeatUserPage(deviceId, pageNumber, pageSize) { // 获取体温异常人员-点位实时状态
    return request.post(this.baseurl + 'localtionCheck/getHeatUserPage', {
      deviceId: deviceId,
      page: {
        pageNumber: pageNumber,
        pageSize: pageSize
      }
    })
  }

  // personnelBehavior
  getAreaData(deviceId) {
    return request.post(this.baseurl + 'userTrack/getAreaData', {
      deviceId: deviceId
    })
  }

  getBehaviorTracks(recRecordId, recDatetime, pageNumber, pageSize) {
    return request.post(this.baseurl + 'userTrack/getBehaviorTracks', {
      recRecordId: recRecordId,
      recDatetime: recDatetime,
      page: {
        pageNumber: pageNumber,
        pageSize: pageSize
      }
    })
  }

  getLatestAbonormalTemperature(deviceId) {
    return request.post(this.baseurl + 'cityCheck/getLatestAbnormalTemperature', {
      deviceId: deviceId
    })
  }

  userTrackGetEastes(recRecordId, recDatetime) { // 获取体温检测统计数据-成都市新冠肺炎动态
    return request.post(this.baseurl + 'userTrack/getEstates', {
      recRecordId: recRecordId,
      recDatetime: recDatetime
    })
  }
}
