<script>
import loadMap from '../../utils/loadMap'
import sHeader from '../commom/header'
import { DataSvr } from '../../service/DataSvr'

export default {
  components: {
    sHeader
  },
  data() {
    return {
      DataSvr: new DataSvr(),
      title: '发热检测分布图',
      time: ' 2020-02-12 14:29:10',
      SelectionPeopleIndex: 'null',
      map: null, // 地图实例
      plugins: [
        // 加载的一些插件
        'AMap.OverView',
        'AMap.MouseTool',
        'AMap.PolyEditor',
        'AMap.RectangleEditor',
        'AMap.PlaceSearch',
        'AMap.DistrictLayer',
        'AMap.CustomLayer',
        'AMap.DistrictSearch'
      ],
      key: '2aa75a57e86358261a7bae9545640490',
      v: '1.4.14', // 地图版本
      colors: {}, // 地图行政区
      loading: true, // 地图loading
      markers: [], // 点标集合
      markersList: [],
      recRecordList: [], // 体温监测数据列表数据
      mapList: [], // 地图数据
      heatTrendStatisticsList: [], // 体温高热趋势图数据
      heatDistributeStatisticsList: [], // 体温高热人数分布数据
      epidemicStatisticsData: {}, // 行业疫情数据
      areaId: 510000,
      originData: null,
      name: '四川省',
      center: [104.04339, 30.641982],
      type: false
    }
  },
  watch: {
    heatDistributeStatisticsList() {
      this.timer()
    }
  },
  mounted() {
    if (this.$route.query.areaId) {
      this.areaId = this.$route.query.areaId
      this.getPos()
    }
    this.getEastesData(this.areaId)
    this.getHeatUserData(this.areaId)
    this.getHeatDistributeStatisticsData(this.areaId)
    if (this.areaId === 510000) {
      this.getCityHeatDistributeStatisticsData(this.areaId)
    }
    this.getEpidemicStatisticsData(this.areaId)
    this.initMap()
  },
  methods: {
    // 定时器
    timer() {
      clearTimeout(this.timer)
      return setTimeout(() => {
        this.getHeatUserData(this.areaId)
      }, 3000)
    },
    // 获取初始经纬度等
    getPos() {
      this.DataSvr.getPosition(this.$route.query.areaId)
        .then(res => {
          this.originData = res
          this.name = this.originData.areaName
          this.type = this.originData.hasChildren
          this.title = `${this.name}发热检测分布图`
          this.center = [this.originData.lng, this.originData.lat]
          this.getCityHeatDistributeStatisticsData(this.originData.areaId)
        })
        .catch()
    },
    // 获取体温监测数据列表数据
    getHeatUserData(id) {
      this.DataSvr.getHeatUser(id)
        .then(res => {
          this.recRecordList = [...res.records]
        })
        .catch()
    },
    // 获取地图点位信息
    getEastesData(id) {
      this.DataSvr.getMapEastes(id)
        .then(res => {
          this.mapList = res
          this.mapList.forEach(item => {
            const pos = this.bMapTransQQMap(item.lng, item.lat)
            const img =
                item.temperatureAbnormalNum > 0
                  ? require('../../assets/image/dynamic/red.gif')
                  : require('../../assets/image/dynamic/green.gif')
            const markerItem = {
              icon: img,
              position: [pos.lng, pos.lat],
              estateId: item.id,
              name: item.name
            }
            this.markers.push(markerItem)
          })
        })
        .catch()
    },
    // 获取体温高热趋势图数据
    getHeatDistributeStatisticsData(id) {
      this.DataSvr.getHeatDistributeStatistics(id)
        .then(res => {
          this.heatTrendStatisticsList = res
          if (this.heatTrendStatisticsList) {
            const timeArr = []
            const numArr = []
            this.heatTrendStatisticsList.forEach(item => {
              timeArr.push(this.$moment(item.date).format('MM-DD'))
              numArr.push(item.temperatureHeatTotal)
            })

            this.showLineChart(timeArr, numArr)
          }
        })
        .catch()
    },
    // 获取体温高热人数分布数据
    getCityHeatDistributeStatisticsData(id) {
      this.DataSvr.getCityHeatDistributeStatistics(id)
        .then(res => {
          this.heatDistributeStatisticsList = res
          if (this.heatDistributeStatisticsList) {
            this.heatDistributeStatisticsList.length = 6
          }
        })
        .catch()
    },
    // 获取行业疫情数据
    getEpidemicStatisticsData(id) {
      this.DataSvr.getEpidemicStatistics(id)
        .then(res => {
          this.epidemicStatisticsData = res
        })
        .catch()
    },
    // 初始化地图
    initMap() {
      loadMap(this.key, this.plugins, this.v)
        .then(AMap => {
          // 城市分块
          /*
          const disProvince = new AMap.DistrictLayer.Province({
            zIndex: 12,
            adcode: [this.areaId], // 成都id
            depth: 1, // 区域等级 0省级 1市级 2区县级
            disProvince: null, // 区域图层
            styles: {
              fill: function(properties) {
                var adcode = properties.adcode
                return getColorByAdcode(adcode)
              },
              'province-stroke': 'cornflowerblue',
              'city-stroke': 'white', // 中国地级市边界
              'county-stroke': 'rgba(255,255,255,0.5)' // 中国区县边界
            }
          })
          */
          // 绘制地图
          this.map = new AMap.Map('map', {
            resizeEnable: true, // 是否监控地图容器尺寸变化
            mapStyle: 'amap://styles/darkblue',
            center: this.center,
            zoom: 10 // 初始化图层基本
          })
          // 地图渲染完成，关闭loading
          this.map.on('complete', () => {
            this.loading = false
            this.addMarker(this.map, this.markers, this.markersList)
            this.initPro(this.map)
          })
        })
        .catch(() => {
          console.log('地图加载失败！')
        })
    },
    // 绘制遮罩
    initPro(map) {
      new AMap.DistrictSearch({
        extensions: 'all',
        subdistrict: 0
      }).search(this.name, function(status, result) {
        // 外多边形坐标数组和内多边形坐标数组
        var outer = [
          new AMap.LngLat(-360, 90, true),
          new AMap.LngLat(-360, -90, true),
          new AMap.LngLat(360, -90, true),
          new AMap.LngLat(360, 90, true)
        ]
        var holes = result.districtList[0].boundaries

        var pathArray = [outer]
        pathArray.push.apply(pathArray, holes)
        var polygon = new AMap.Polygon({
          pathL: pathArray,
          strokeColor: '#00eeff',
          strokeWeight: 1,
          fillColor: '#71B3ff',
          fillOpacity: 0.5
        })
        polygon.setPath(pathArray)
        map.add(polygon)
      })
    },
    // 创建点标记
    addMarker(map, arr, arrList) {
      arr.forEach(marker => {
        var markerItem = new AMap.Marker({
          map: map,
          icon: new AMap.Icon({
            image: marker.icon,
            size: new AMap.Size(26, 26), // 图标大小
            imageSize: new AMap.Size(26, 26)
          }),
          position: [marker.position[0], marker.position[1]],
          offset: new AMap.Pixel(-13, -30)
        })
        arrList.push(markerItem)
      })
      this.map.setFitView()
    },
    // 折线图
    showLineChart(timeArr, numArr) {
      const lineChart = this.$echarts.init(document.getElementById('line'))
      lineChart.setOption({
        grid: {
          top: '20%',
          left: '4%',
          right: '5%',
          bottom: '4%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: timeArr,
          axisLine: {
            lineStyle: {
              // 设置x轴颜色
              color: '#fff'
            }
          }
        },
        yAxis: {
          type: 'value',
          min: 0, // 设置y轴刻度的最小值
          max: Math.max.apply(null, numArr), // 设置y轴刻度的最大值
          axisLine: {
            lineStyle: {
              // 设置x轴颜色
              color: '#fff'
            }
          }
        },
        series: [
          {
            data: numArr,
            type: 'line',
            areaStyle: {}
          }
        ],
        color: ['#1FF1FF']
      })
    },
    SelectionPeople(val) {
      this.SelectionPeopleIndex = val
    },
    // 地图转换
    bMapTransQQMap(lng, lat) {
      const x_pi = (3.14159265358979324 * 3000.0) / 180.0
      const x = lng - 0.0065
      const y = lat - 0.006
      const z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * x_pi)
      const theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * x_pi)
      const lngs = z * Math.cos(theta)
      const lats = z * Math.sin(theta)

      return {
        lng: lngs,
        lat: lats
      }
    }
  },
  destroyed() {
    clearTimeout(this.timer)
  }
}
</script>

