<script>
import sHeader from '../commom/header'
import loadMap from '../../utils/loadMap'
import { DataSvr } from '../../service/DataSvr'

export default {
  components: {
    sHeader
  },
  data() {
    return {
      DataSvr: new DataSvr(),
      title: '',
      map: null, // 地图实例
      plugins: [
        // 加载的一些插件
        'AMap.OverView',
        'AMap.MouseTool',
        'AMap.PolyEditor',
        'AMap.RectangleEditor',
        'AMap.PlaceSearch',
        'AMap.DistrictLayer',
        'AMap.CustomLayer',
        'AMap.DistrictSearch'
      ],
      key: '2aa75a57e86358261a7bae9545640490',
      v: '1.4.14', // 地图版本
      colors: {}, // 地图行政区
      loading: true, // 地图loading
      markers: [], // 点标集合
      markersList: [],
      getHeatUserPageData: [], // 他的行为轨迹
      personalData: '', // 当前个人数据
      mapList: [], // 地图列表,
      recRecordId: '',
      recDatetime: '',
      timer: null, // 倒计时
      reloadTimer: null, // 刷新页面计时器
      deviceId: '',
      mapData: ''
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    if (process.env.NODE_ENV === 'development') {
      // 开发模式 东方天呈ID
      this.deviceId = '3d406cc7-53d4-432c-be79-ace661712935 '
    } else {
      this.deviceId = this.$route.query.deviceId
    }
    // console.log(this.indexId);
    // console.log(this.estateId);
    // console.log(this.title);
    // this.title = this.$route.query.name;
    // let recRecordId = this.$route.query.recRecordId || 'dc6bb898-0cf7-4203-8b7d-09ad072f4564';
    // let recDatetime = this.$route.query.recDatetime || '2020-02-19 15:50:27';
    // if (localStorage.getItem('abnormal')) {
    //     recRecordId = JSON.parse(localStorage.getItem('abnormal')).id || "dc6bb898-0cf7-4203-8b7d-09ad072f4564";
    //     recDatetime = JSON.parse(localStorage.getItem('abnormal')).recDatetime || "2020-02-19 15:50:27";
    // }
    if (this.reloadTimer) {
      clearInterval(this.reloadTimer)
    } else {
      this.reloadTimer = setInterval(() => {
        location.reload()
      }, 1000 * 60 * 60)
    }
    if (this.deviceId) {
      this.getAreaData()
      this.getLatestAbonormalTemperature()
      this.getDeviceAddressById()
    }
  },
  methods: {
    //  获取经纬度
    getAreaData() {
      this.DataSvr.getAreaData(this.deviceId).then(res => {
        if (res) {
          this.mapData = res
          this.initMap(res)
        }
      }).catch()
    },
    // 获取title
    getDeviceAddressById() {
      this.DataSvr.getDeviceAddressById(this.deviceId).then(res => {
        if (res) {
          this.title = res + '人员行为轨迹分析'
        }
      }).catch()
    },
    // 异常人数据
    getLatestAbonormalTemperature() {
      clearInterval(this.timer)
      this.DataSvr.getLatestAbonormalTemperature(this.deviceId)
        .then(res => {
          this.recRecordId = res.id
          this.recDatetime = res.recDatetime
          this.personalData = res
          if (this.recDatetime && this.recRecordId) {
            this.behaviorTrajectoryData(this.recRecordId, this.recDatetime)
            this.getEastesBy(this.recRecordId, this.recDatetime)
          }
          this.timer = setInterval(() => {
            this.getLatestAbonormalTemperature()
          }, 3000)
        })
        .catch(() => {
          clearInterval(this.timer)
        })
    },
    // 地图数据
    // 初始化地图
    initMap(res) {
      loadMap(this.key, this.plugins, this.v)
        .then(AMap => {
          // 城市分块
          // const disProvince = new AMap.DistrictLayer.Province({
          //   zIndex: 12,
          //   adcode: [res.areaId], // 成都id
          //   depth: 1, // 区域等级 0省级 1市级 2区县级
          //   disProvince: null, // 区域图层
          //   styles: {
          //     fill: function(properties) {
          //       var adcode = properties.adcode
          //       return getColorByAdcode(adcode)
          //     },
          //     'province-stroke': 'cornflowerblue',
          //     'city-stroke': 'white', // 中国地级市边界
          //     'county-stroke': 'rgba(255,255,255,0.5)' // 中国区县边界
          //   }
          // })
          // 绘制地图
          this.map = new AMap.Map('map', {
            resizeEnable: true, // 是否监控地图容器尺寸变化
            mapStyle: 'amap://styles/darkblue',
            center: [res.lng, res.lat],
            // zooms: [7, 10],
            zoom: 10 // 初始化图层基本
          })
          // 地图渲染完成，关闭loading
          this.map.on('complete', () => {
            this.loading = false
            this.addMarker(this.map, this.markers, this.markersList)
            this.initPro(this.map)
            this.map.setFitView() // 调整地图到合适的缩放等级和中心点
          })
        })
        .catch(() => {
          console.log('地图加载失败！')
        })
    },
    // 绘制遮罩
    initPro(map) {
      new AMap.DistrictSearch({
        extensions: 'all',
        subdistrict: 0
      }).search(this.mapData.areaName, function(status, result) {
        // 外多边形坐标数组和内多边形坐标数组
        var outer = [
          new AMap.LngLat(-360, 90, true),
          new AMap.LngLat(-360, -90, true),
          new AMap.LngLat(360, -90, true),
          new AMap.LngLat(360, 90, true)
        ]
        var holes = result.districtList[0].boundaries

        var pathArray = [outer]
        pathArray.push.apply(pathArray, holes)
        var polygon = new AMap.Polygon({
          pathL: pathArray,
          strokeColor: '#00eeff',
          strokeWeight: 1,
          fillColor: '#71B3ff',
          fillOpacity: 0.5
        })
        polygon.setPath(pathArray)
        map.add(polygon)
      })
    },
    // 创建点标记
    addMarker(map, arr, arrList) {
      arr.forEach(marker => {
        var markerItem = new AMap.Marker({
          map: map,
          content: `<div class="tips">${marker.num}</div>`,
          position: [marker.position[0], marker.position[1]],
          offset: new AMap.Pixel(-13, -30)
        })
        markerItem.setLabel({
          offset: new AMap.Pixel(-10, 20), // 设置文本标注偏移量
          content: `<div class="map-info"><div class="map-address">${marker.name}</div><div class="map-time">检测时间：${marker.time}</div></div>`, // 设置文本标注内容
          direction: 'right' // 设置文本标注方位
        })
        arrList.push(markerItem)
      })
    },
    // 行为轨迹数据
    behaviorTrajectoryData(recRecordId, recDatetime) {
      this.DataSvr.getBehaviorTracks(recRecordId, recDatetime, 1, 10)
        .then(res => {
          this.getHeatUserPageData = res
        })
        .catch()
    },
    // 获取体温检测统计数据-成都市新冠肺炎动态
    getEastesBy(recRecordId, recDatetime) {
      this.markers = []
      this.DataSvr.userTrackGetEastes(recRecordId, recDatetime)
        .then(res => {
          this.mapList = res
          this.mapList.forEach(item => {
            const pos = this.bMapTransQQMap(item.lng, item.lat)
            const markerItem = {
              num: item.temperatureAbnormalNum,
              position: [pos.lng, pos.lat],
              name: item.name,
              time: item.recDatetime
            }
            this.markers.push(markerItem)
          })
          this.addMarker(this.map, this.markers, this.markersList)
        })
        .catch()
    },
    // 地图转换
    bMapTransQQMap(lng, lat) {
      const x_pi = (3.14159265358979324 * 3000.0) / 180.0
      const x = lng - 0.0065
      const y = lat - 0.006
      const z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * x_pi)
      const theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * x_pi)
      const lngs = z * Math.cos(theta)
      const lats = z * Math.sin(theta)
      return {
        lng: lngs,
        lat: lats
      }
    },
    // 刷新页面
    reload() {
      const t = new Date()
      const now = t.toLocaleTimeString().replace(/^\D*/, '')
      const num = now.indexOf(':')
      const time = now.substring(0, num)
      console.log(time)
    }

  },
  beforeDestroy() {
    clearInterval(this.timer)
    clearInterval(this.reloadTimer)
  },
  destroyed() {
    clearInterval(this.timer)
    clearInterval(this.reloadTimer)
  }
}
</script>

